import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import {
    ICreatePedidoPresupuesto,
    IItemPedido,
    IPedidoDePresupuesto,
} from '../../models/pedido-de-presupuesto';
import { PedidoDePresupuestoService } from '../../service/pedidos-de-presupuesto.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import {
    MAT_DIALOG_DATA,
    MatDialog,
    MatDialogRef,
} from '@angular/material/dialog';
import {
    BehaviorSubject,
    catchError,
    debounceTime,
    distinctUntilChanged,
    finalize,
    firstValueFrom,
    map,
    Observable,
    of,
    startWith,
    Subject,
    switchMap,
    takeUntil,
    takeWhile,
    tap,
    timer,
} from 'rxjs';
import { EmpresaService } from '@shared/service/app/empresa.service';
import { IProveedor } from 'app/modules/administracion/proveedores/models/proveedor';
import { DatePipe } from '@angular/common';
import { IArticulo } from 'app/modules/stock/articulos/models/articulo';
import { MatTableDataSource } from '@angular/material/table';
import { MatAutocompleteSelectedEvent } from '@angular/material/autocomplete';
import { LectorQrComponent } from '@shared/components/lector-qr/lector-qr.component';
import { MY_DATE_FORMATS } from '@shared/constants/date-format';
import { MAT_DATE_FORMATS } from '@angular/material/core';
import { IAlertConfig } from '@shared/models/alert';
import { IRubro } from 'app/modules/stock/rubros/models/rubro';
import { ISelectResponse } from '@shared/models/select-response';

@Component({
    selector: 'app-save-pedidos-de-presupuesto',
    templateUrl: './save.component.html',
    styleUrls: ['./save.component.scss'],
    providers: [{ provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS }],
})
export class SaveComponent implements OnInit {
    // Para el funcionamiento del sistema
    title: string;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    maxPalabras: number = 50;
    minDate: Date;
    maxDate: Date;
    showAlert = false;
    _alertConfig: IAlertConfig;
    mostrarSpinner = true;
    mostrar_mensaje: boolean = false;

    @Output() public creadoExitosamente: EventEmitter<void> =
        new EventEmitter<void>();
    @Output() public editadoExitosamente: EventEmitter<void> =
        new EventEmitter<void>();

    // Propios del modulo
    public dataSource: MatTableDataSource<any> = new MatTableDataSource();
    columns = [
        'codigointerno',
        'articulo',
        'cantidad',
        'unidadmedida',
        'detalle',
        'acciones',
    ];
    selectedProveedores: IProveedor[] = [];
    form_carga_articulos: FormGroup;
    form: FormGroup;
    pedido_de_presupuesto: IPedidoDePresupuesto;
    mov_art_list: any[] = [];
    articulos: IArticulo[] = [];
    articulos_list = [];
    unidades_de_medida: any[] = [];
    proveedores: IProveedor[];
    filteredProveedores$: Observable<IProveedor[]>;
    filteredArticulos$: BehaviorSubject<IArticulo[]> = new BehaviorSubject([]);
    rubros: IRubro[] = [];
    filteredRubros$: BehaviorSubject<ISelectResponse[]> = new BehaviorSubject(
        []
    );
    isHovering: boolean;

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _pedidoDePresupuestoService: PedidoDePresupuestoService,
        private _empresaService: EmpresaService,
        public alert: AppAlertService,
        private datePipe: DatePipe,
        public dialogRef: MatDialogRef<SaveComponent>,
        private dialogo: MatDialog
    ) {
        this.pedido_de_presupuesto = this.data ? this.data : null;
        this.title = this.pedido_de_presupuesto?.idpedido
            ? 'Editar pedido de presupuesto'
            : 'Nuevo pedido de presupuesto';
        // Se define minDate como null o cualquier otra fecha límite que desees permitir hacia atras
        this.minDate = null;
        // Define maxDate como el dia de hoy
        this.maxDate = new Date();
        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe((empresa) => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });
    }

    ngOnInit(): void {
        this.newForm();
        this.subscribeAlert();
        this.loadServices();

        // Escuchamos los cambios en el control de 'articulo' para filtrar mientras escribe
        this.form_carga_articulos
            .get('articulo')
            .valueChanges.pipe(
                debounceTime(500), // Evita demasiadas llamadas consecutivas
                distinctUntilChanged(), // Solo procede si el valor cambia
                switchMap((value) => {
                    const idarticulo =
                        typeof value === 'object' ? value.idarticulo : null;
                    const filterLike = typeof value === 'string' ? value : null;
                    let idrubro = this.form_carga_articulos.get('rubro').value
                        ? this.form_carga_articulos.get('rubro').value.idrubro
                        : null;

                    return this._pedidoDePresupuestoService
                        .getArticulos(idarticulo, filterLike, idrubro)
                        .pipe(
                            catchError(() => of({ list: [] })) // Manejo de errores
                        );
                })
            )
            .subscribe((response) => {
                this.filteredArticulos$.next(response.list || []);
            });
    }

    onHover(hovering: boolean, $event: MouseEvent) {
        $event.preventDefault();
        this.isHovering = hovering;
    
        const target = $event.target as HTMLElement;
        const span = target.querySelector('span');

        if (span) {
            span.style.color = hovering ? 'white' : '';
        }
    
        if (hovering) {
            target.style.setProperty('background-color', this.color_primario, 'important');
        } else {
            target.style.removeProperty('background-color');
            target.style.removeProperty('color');
        }
    }
    

    /**
     * Este metodo ejecuta todas las peticiones asincronas al mismo tiempo
     */
    async loadServices() {
        await Promise.all([
            this.getProveedores(), // Método que carga proveedores
            this.getArticulos(), // Método que carga articulos
            this.getRubros(), // Método que carga rubros
        ]).then(() => {
            this.mostrarSpinner = false;
        });
    }

    // Alert
    subscribeAlert() {
        this.alert.emitConfig.subscribe({
            next: (config: IAlertConfig) => {
                this.showAlert = true;
                this._alertConfig = {
                    ...config,
                    appearance: 'border',
                };
                let time = 2;
                timer(100, 1500)
                    .pipe(
                        finalize(() => {
                            this.showAlert = false;
                            this._alertConfig = null;
                        }),
                        takeWhile(() => time > 0),
                        takeUntil(this._unsubscribeAll),
                        tap(() => (time -= 1))
                    )
                    .subscribe();
            },
        });
    }

    newForm() {
        const [dia, mes, anio] = this.pedido_de_presupuesto
            ? this.pedido_de_presupuesto.fecha.split('/')
            : ''; //Se hace esto para poder manejar el formato que se cambio en el render del componente de tabla dinamica.

        if (this.pedido_de_presupuesto) {
            //Sí existe el pedido de presupuesto, cargo la grilla con sus articulos...
            this.getItemsPedido(this.pedido_de_presupuesto.idpedido).then(
                (res) => {
                    this.mov_art_list = res.map((m: IItemPedido) => {
                        return {
                            articulo: m.idarticulo,
                            cantidad: m.cantidad,
                            idunidadmedida: m.idunidadmedida,
                            descripcion_articulo: m.articulo_descripcion,
                            descripcion_um: m.um_descripcion,
                            codigo_interno_articulo: m.codigo_interno,
                            detalle: m.detalle,
                        };
                    });

                    this.dataSource = new MatTableDataSource(this.mov_art_list);
                }
            );
        }

        this.form = new FormGroup({
            fecha: new FormControl(
                this.pedido_de_presupuesto?.fecha
                    ? this.datePipe.transform(
                          new Date(`${mes}/${dia}/${anio}`),
                          'yyyy-MM-dd'
                      )
                    : this.datePipe.transform(new Date(), 'YYYY-MM-dd'),
                [Validators.required]
            ),
            proveedor: new FormControl(''),
            observaciones: new FormControl(
                this.pedido_de_presupuesto?.observaciones
                    ? this.pedido_de_presupuesto?.observaciones
                    : null
            ),
        });

        this.form_carga_articulos = new FormGroup({
            rubro: new FormControl(''),
            articulo: new FormControl('', Validators.required),
            unidadmedida: new FormControl('', Validators.required),
            cantidad: new FormControl('', [
                Validators.required,
                Validators.min(1),
            ]),
            detalle: new FormControl(''),
        });

        this.updateFormValidators();
    }

    selectProveedor(
        $event: MatAutocompleteSelectedEvent,
        input: HTMLInputElement
    ): void {
        const selected = $event.option.value;

        if (selected && !this.selectedProveedores.includes(selected)) {
            this.selectedProveedores.push(selected);
        }

        this.form.get('proveedor').setValue('');
        input.value = '';
    }

    removeProveedor(proveedor: IProveedor): void {
        const index = this.selectedProveedores.indexOf(proveedor);
        if (index >= 0) {
            this.selectedProveedores.splice(index, 1);
        }
    }

    onSubmit() {
        if (!this.form.valid) {
            return this.alert.warning('Revise Los Datos Ingresados');
        } else {
            const fecha = this.form.get('fecha').value;
            const fecha_formateada = this.datePipe.transform(
                fecha,
                'yyyy-MM-dd'
            );

            const items_pedido = this.mov_art_list.map((a) => {
                return {
                    idarticulo: a.articulo,
                    idunidadmedida: a.idunidadmedida,
                    cantidad: a.cantidad,
                    detalle: a.detalle,
                };
            });

            const proveedores_id = this.selectedProveedores.map((p) => {
                return p.Codigo;
            });

            const payload: ICreatePedidoPresupuesto = {
                pedido: {
                    fecha: fecha_formateada,
                    observaciones:
                        this.form.get('observaciones').value != null &&
                        this.form.get('observaciones').value != ''
                            ? this.form.get('observaciones').value
                            : null,
                },
                articulos: items_pedido,
                proveedores: proveedores_id,
            };

            if (this.pedido_de_presupuesto?.idpedido) {
                this._pedidoDePresupuestoService
                    .updatePedidoDePresupuesto(
                        this.pedido_de_presupuesto.idpedido,
                        payload
                    )
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.onCloseMenu();
                            }
                        },
                    });
            } else {
                this._pedidoDePresupuestoService
                    .savePedidoDePresupuesto(payload)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.onCloseMenu();
                            }
                        },
                    });
            }
        }
    }

    /**
     * Esta funcion maneja las cambios de seleccion que puedan tener los combos.
     * @param $event
     */
    seleccionItem($event, info?) {
        //Al seleccionar un articulo...
        if ($event.idarticulo) {
            this.getUnidadesDeMedidaArticulos($event.idarticulo); //...Buscamos las UM que posee ese articulo.

            // Muevo el foco al campo "cantidad" luego de un pequeño retraso.
            // Lo encapsulo dentro de `setTimeout` porque espero a que el DOM renderice el input "cantidad".
            setTimeout(() => {
                const cantidadField = document.querySelector(
                    '[formControlName="cantidad"]'
                ) as HTMLInputElement;
                cantidadField?.focus();
            }, 1);
        }

        if ($event.idrubro && !$event.idarticulo) {
            this.getArticulos($event.idrubro);

            // Muevo el foco al campo "articulo" luego de un pequeño retraso.
            // Lo encapsulo dentro de `setTimeout` porque espero a que el DOM renderice el input "articulo".
            setTimeout(() => {
                const articuloField = document.querySelector(
                    '[formControlName="articulo"]'
                ) as HTMLInputElement;
                articuloField?.focus();
            }, 1);
        }

        if ($event && info === 'cambio_de_foco') {
            $event.preventDefault();

            // Muevo el foco al campo "detalle" luego de un pequeño retraso.
            // Lo encapsulo dentro de `setTimeout` porque espero a que el DOM renderice el input "detalle".
            setTimeout(() => {
                const detalleField = document.querySelector(
                    '[formControlName="detalle"]'
                ) as HTMLInputElement;
                detalleField?.focus();
            }, 1);
        }
    }

    /**
     * Funcion para limpiar campos
     */
    limpiarCasillero(input) {
        if (this.form.get(input)) {
            this.form.get(input).setValue('');
            this.seleccionItem(input, 'reset');
        } else if (this.form_carga_articulos.get(input)) {
            this.form_carga_articulos.get(input).setValue('');
            this.seleccionItem(input, 'reset');
        }

        if (input === 'rubro') {
            if (this.form_carga_articulos.get('articulo').value) {
                this.form_carga_articulos.get('articulo').setValue('');
            }
            this.getArticulos();
        }

        if (input === 'articulo') {
            this.form_carga_articulos.get('unidadmedida').setValue('');
            this.form_carga_articulos.get('articulo').setValue('');
            this.form_carga_articulos.get('cantidad').setValue('');
        }

        if (input === 'carga_exitosa') {
            this.form_carga_articulos.get('unidadmedida').setValue('');
            this.form_carga_articulos.get('articulo').reset({ value: '' });
            this.form_carga_articulos.get('cantidad').setValue('');
            this.form_carga_articulos.get('detalle').setValue('');
        }

        if (input === 'proveedor') {
            this.selectedProveedores = [];
            this.form.get('proveedor').setValue(null);
        }
    }

    // ################# Carga De Movimientos #################

    /**
     * Esta funcion añade un nuevo item a la grilla dentro del saveComponent.
     * @var movimiento_articulo Encapsula un objeto con los datos que deseamos mostrar en la tabla.
     */
    addMovimiento(movimiento) {
        if (movimiento) {
            //Si 'res' no es false | undefined | null, continua con la logica...
            let movimiento_articulo = {
                //Encapsulo los datos que me interesan mostrar en la grilla.
                articulo: movimiento.articulo.idarticulo,
                cantidad: movimiento.cantidad,
                idunidadmedida: movimiento.unidad_medida,
                descripcion_articulo: movimiento.articulo.descripcion,
                descripcion_um: movimiento.unidadmedida_descripcion,
                codigo_interno_articulo: movimiento.articulo.codigo_interno,
                detalle: movimiento.detalle,
            };
            this.mov_art_list = [movimiento_articulo, ...this.mov_art_list]; // Se agrega el nuevo articulo al comienzo de la lista para que siempre aparezca primero en la grilla
            this.dataSource = new MatTableDataSource(this.mov_art_list); //Actualizo la tabla

            // Actualizar validaciones según el estado de la grilla
            this.updateFormValidators();

            this.limpiarCasillero('carga_exitosa'); // Limpio casillerros para que este listo para volver a cargar
            this.alert.success('Artículo añadido con éxito');
        }
    }

    /**
     * El resultado de esta operación es un nuevo
     * array que contiene todos los elementos de la lista original,
     * excepto el artículo que se desea eliminar.
     * @param articulo
     */
    deleteMovimiento(articulo) {
        this.mov_art_list = this.mov_art_list.filter((item) => {
            return item !== articulo;
        });
        this.dataSource = new MatTableDataSource(this.mov_art_list);

        if (this.mov_art_list.length === 0) {
            this.form.get('presupuesto').enable();
        }

        this.alert.success('Artículo eliminado con éxito');
    }

    onCargar($event?: KeyboardEvent, input?: string) {
        if ($event?.key === 'Enter') {
            $event.preventDefault();
        }

        if (this.form_carga_articulos.get('cantidad').value <= 0) {
            return this.alert.warning(
                'Por favor, Ingrese una cantidad mayor a cero'
            );
        }

        if (
            !this.form_carga_articulos.get('unidadmedida').value ||
            !this.form_carga_articulos.get('articulo').value ||
            !this.form_carga_articulos.get('cantidad').value
        ) {
            return this.alert.warning(
                'Por favor, complete todos los campos de la carga de articulo'
            );
        }

        if (!this.form_carga_articulos.valid) {
            return this.alert.error('Debe seleccionar un artículo');
        } else {
            let existeMovimiento = this.mov_art_list.find(
                (movimiento) =>
                    movimiento.articulo ===
                        this.form_carga_articulos.get('articulo').value
                            .idarticulo &&
                    movimiento.idunidadmedida ===
                        this.form_carga_articulos.get('unidadmedida').value
            );

            if (existeMovimiento) {
                this.form_carga_articulos.get('unidadmedida').setValue('');
                return this.alert.warning(
                    'Ya existe un movimiento de ese articulo con esa unidad de medida'
                );
            }

            let unidadmedida = this.unidades_de_medida.find(
                (um) =>
                    um.idunidadmedida ===
                    this.form_carga_articulos.get('unidadmedida').value
            ); //Traigo una UM que coincida con mi seleccion para poder utilizar su descripcion.

            let movimiento_articulo = {
                articulo: this.form_carga_articulos.get('articulo').value,
                unidad_medida:
                    this.form_carga_articulos.get('unidadmedida').value,
                unidadmedida_descripcion: unidadmedida.descripcion_um,
                detalle: this.form_carga_articulos.get('detalle').value,
                cantidad: this.form_carga_articulos.get('cantidad').value,
            };

            this.addMovimiento(movimiento_articulo);
        }

        // Mueve el foco al campo "articulo" después de la carga
        this.form_carga_articulos.get('articulo').reset({ value: '' });
        const articuloField = document.querySelector(
            '[formControlName="articulo"]'
        ) as HTMLInputElement;
        articuloField?.focus();
    }

    onCloseMenu() {
        this.dialogRef.close();
    }

    /**
     * Se escanea el QR de la Etiqueta de articulo y se toma el idarticulo.
     */
    etiquetaArticulo() {
        this.dialogo
            .open(LectorQrComponent, {
                panelClass: 'ayuda-dialog',
            })
            .componentInstance.sendObject.subscribe((code) => {
                if (!code || code.length === 0) {
                    return this.alert.error('Código QR vacío o inválido');
                }

                let decodedText = JSON.parse(code); // Convierto el string en un json

                const idarticulo = parseInt(decodedText['idarticulo'], 10);
                this._pedidoDePresupuestoService
                    .getArticulos(idarticulo)
                    .subscribe(
                        (response) => {
                            if (response?.list?.length > 0) {
                                const articulo = response.list[0];
                                this.form_carga_articulos
                                    .get('articulo')
                                    .setValue(articulo);
                            } else {
                                return this.alert.error(
                                    'No hay stock del artículo en el depósito'
                                );
                            }
                        },
                        (error) => {
                            console.error('Error al obtener artículo:', error);
                        }
                    );
            });
    }

    // ################# Metodos de comunicacion con el service #################

    async getProveedores(option?) {
        const Proveedores = await firstValueFrom(
            this._pedidoDePresupuestoService.getProveedores()
        );

        new Promise(async (resolve) => {
            this.proveedores = Proveedores.list;
            this.filteredProveedores$ = this.form
                .get('proveedor')
                .valueChanges.pipe(
                    startWith(''),
                    map((value) => this._filterProveedores(value))
                );

            // En caso de edicion...
            if (this.pedido_de_presupuesto) {
                const id_proveedores = this.pedido_de_presupuesto.proveedores
                    .toString()
                    .split(','); // Encapsulo los idproveedor que me envia el servicio en un arreglo de tipo String...

                // Por cada idproveedor...
                id_proveedores.forEach((idproveedor) => {
                    // Busco el proveedor el cual coincida su 'codigo' y el 'idproveedor' parseado a Entero...
                    let proveedor = this.proveedores.find(
                        (p: IProveedor) => p.Codigo === parseInt(idproveedor)
                    );

                    // Si no existe el proveedor en arreglo que lista los chips...
                    if (!this.selectedProveedores.includes(proveedor)) {
                        // Lo agrego.
                        this.selectedProveedores.push(proveedor);
                    }
                    // Sino no hace nada...
                });
                // Termina ejecucion...
            }

            resolve(true);
        });
    }

    async getArticulos(idrubro?: number | null) {
        try {
            const articulos = await firstValueFrom(
                this._pedidoDePresupuestoService.getArticulos(
                    null,
                    null,
                    idrubro
                )
            );

            this.articulos = articulos.list;
            this.articulos_list = this.articulos;

            this.form_carga_articulos
                .get('articulo')
                .valueChanges.pipe(
                    startWith(''),
                    map((value) => this._filterArticulos(value || ''))
                )
                .subscribe((filtered) => {
                    this.filteredArticulos$.next(filtered);
                });
        } catch (error) {
            console.error('Error al obtener articulos:', error);
            this.filteredArticulos$.next([]);
        }
    }

    async getUnidadesDeMedidaArticulos(idarticulo) {
        const unidadesDeMedida = await firstValueFrom(
            this._pedidoDePresupuestoService.getUnidadesDeMedidaArticulos(
                idarticulo
            )
        );
        new Promise(async (resolve) => {
            let um_base; // Asigno una variable global para que cada UM sepa cual es la um base (factor = '1.00')
            this.unidades_de_medida = unidadesDeMedida.list
                .sort((a, b) => a.factor - b.factor)
                .map((unidad) => {
                    //Convierto en numero
                    unidad.factor = parseFloat(unidad.factor);

                    // Almaceno solo la um base de un articulo
                    if (unidad.factor === 1) {
                        um_base = unidad.um.descripcion;
                    }

                    return {
                        idunidadmedida: unidad.idunidadmedida,
                        descripcion_um: unidad.um.descripcion,
                        factor_um: unidad.factor,
                        um_base: um_base,
                    };
                });

            // Autoasigna la primera unidad de medida al control del formulario
            if (this.unidades_de_medida.length > 0) {
                this.form_carga_articulos
                    .get('unidadmedida')
                    .setValue(this.unidades_de_medida[0].idunidadmedida);
                this.seleccionItem({
                    um: this.unidades_de_medida[0].idunidadmedida,
                });
            }
            resolve(true);
        });
    }

    async getItemsPedido(idpedido): Promise<any> {
        return new Promise(async (resolve) => {
            const items = await firstValueFrom(
                this._pedidoDePresupuestoService.getItemsPedido(idpedido)
            );
            this.mov_art_list = items.list;
            resolve(this.mov_art_list);
        });
    }

    async getRubros() {
        try {
            const rubros = await firstValueFrom(
                this._pedidoDePresupuestoService.getRubros()
            );

            this.rubros = rubros.list;

            this.form_carga_articulos
                .get('rubro')
                .valueChanges.pipe(
                    startWith(''),
                    map((value) => this._filterSelect(value || '', 'rubros'))
                )
                .subscribe((filtered) => {
                    this.filteredRubros$.next(filtered);
                });
        } catch (error) {
            console.error('Error al obtener articulos:', error);
            this.filteredRubros$.next([]);
        }
    }

    private _filterProveedores(proveedor: IProveedor): IProveedor[] {
        const filterValue = proveedor.toString().toLowerCase();
        return this.proveedores.filter((proveedor) =>
            proveedor.RazonSocial.toLowerCase().includes(filterValue)
        );
    }

    // ################# Metodo de filtrado para inputs autocomplete #################

    /**
     * Esta funcion es para filtrar los artículos según el input que se haya ingresado en el buscador de articulos.
     * @param value
     * @returns
     */
    private _filterArticulos(value: string): IArticulo[] {
        const filterValue = value.toString().toLowerCase();
        return this.articulos_list.filter((articulo) => {
            const codigo_interno = articulo.codigo_interno
                .toString()
                .toLowerCase()
                .includes(filterValue);
            const descripcion = articulo.descripcion
                .toString()
                .toLowerCase()
                .includes(filterValue);
            return codigo_interno || descripcion;
        });
    }

    /**
     *
     * @param item Valor a buscar
     * @param model Modelo donde se retorna la lista de valores encontrados
     * @returns Valores encontrados en la lista
     */
    private _filterSelect(
        item: ISelectResponse,
        model?: string
    ): ISelectResponse[] {
        const filterValue = item.toString().toLowerCase();
        return this[`${model}`].filter((item) =>
            item.descripcion.toLowerCase().includes(filterValue)
        );
    }

    // ################# Metodos para obtener textos de input #################

    /**
     * Obtenemos la descripcion del articulo seleccionado.
     * @param articulo
     * @returns
     */
    getTextArticulo(articulo: IArticulo): string {
        return articulo?.idarticulo
            ? `${articulo.codigo_interno} - ${articulo.descripcion}`
            : '';
    }

    getTextProveedor(option) {
        return option ? option.RazonSocial : '';
    }

    getTextGenerico(option) {
        if (option.RazonSocial || option.razonSocial)
            return option ? option.RazonSocial || option.razonSocial : '';

        if (option.descripcion) return option ? option.descripcion : '';

        if (option.nick)
            return option ? `${option.nombre} ${option.apellido}` : '';
    }

    // ################# Metodos extra #################

    /**
     * Funcion para contar las palabras en las observaciones
     */
    getPalabrasLength(): number {
        const observaciones = this.form.get('observaciones').value;
        return observaciones ? observaciones.length : 0;
    }

    /**
     * Funcion para verificar si se ha alcanzado el límite de caracteres en las observaciones
     */
    isPalabraMaxLengthReached(): boolean {
        return this.getPalabrasLength() <= this.maxPalabras;
    }

    /**
     * Este metodo sirve para agregar o quitar las validaciones de requeridos a los campos del formulario de carga de articulos.
     */
    updateFormValidators() {
        if (this.mov_art_list.length === 0) {
            // Si la grilla está vacía, hacer que los campos sean requeridos
            this.form_carga_articulos
                .get('articulo')
                .setValidators(Validators.required);
            this.form_carga_articulos
                .get('unidadmedida')
                .setValidators(Validators.required);
            this.form_carga_articulos
                .get('cantidad')
                .setValidators([Validators.required, Validators.min(1)]);
        } else {
            // Si hay elementos en la grilla, quitar la validación de requerido
            this.form_carga_articulos.get('articulo').clearValidators();
            this.form_carga_articulos.get('unidadmedida').clearValidators();
            this.form_carga_articulos
                .get('cantidad')
                .setValidators(Validators.min(1));
        }

        // Asegurarse de que Angular reevalúe las validaciones
        this.form_carga_articulos.get('articulo').updateValueAndValidity();
        this.form_carga_articulos.get('unidadmedida').updateValueAndValidity();
        this.form_carga_articulos.get('cantidad').updateValueAndValidity();
    }

    /**
     * Filtro para buscar en la grilla de articulos
     * @param filterValue
     */
    applyFilter(filterValue: string): void {
        this.dataSource.filter = filterValue.trim().toLowerCase();
    }
}
